:root{
    --backgroundColor: #201524;
}

body, html{
    background-color: var(--backgroundColor);
}

.character-clickable:hover{
    cursor: pointer;
    background-color: lightblue !important;
}

.game-header{
    border: 2px solid lightblue;
    border-radius: 15px;
    margin-top: 30px;
}

ol .character-name{
    color: white;
    font-weight: 600;
}

.score-board{
    color: #201524;
    width: fit-content;
    background-color: white;
    border-radius: 15px;
    text-align: center;
    height: 80%;
    aspect-ratio: 4/3;
    margin-top: 6%;
    font-weight: 700;
}

.score-header{
    padding-top: 5%;
}
.score{
    font-size: 38px;
    font-weight: 700;
}

ol{
    list-style-type: none;
}

p{
    color: white;
}

.player{
    margin-top: 50px;
    text-align: center;
    justify-content: center;
}

.play-btn-container{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.outcome{
    color: white;
    font-size: 24px;
    font-weight: 900;
}

.play-btn:hover{
    cursor: pointer;
    background-color: var(--backgroundColor) !important;
    border: 1px solid white !important;
    color: white !important;
}

.rule-sheet p{
    color: black;
}

.rules-btn:hover{
    background-color: white;
    color: var(--backgroundColor);
    outline: 2px solid white !important;
    border: 2px solid var(--backgroundColor) !important;
}